body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
	margin: 0;
	box-sizing: border-box;
}

html,
body {
	height: 100%;
	height: 100vh;
}

body {
	background: #f7f7f7;
}

#root {
	height: 100%;
}

.wrapper {
	width: 100%;
	height: 100%;
	height: 100vh;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}

@media (max-width: 800px){
	.wrapper {
		display: block;
		padding: 50px 20px;
	}
}

.new_accounts {
	width: 600px;
	max-width: 100%;
	padding: 50px 25px;
	background: #ffffff;
	box-shadow: 0px 9px 40px -6px rgba(0,0,0,0.25);
}

input {
	display: block;
	margin-bottom: 25px;
	height: 40px;
	line-height: 40px;
	border-radius: 4px;
	width: 100%;
	padding: 0 10px;
	border: 1px solid rgba(0, 0, 0, 0.25);
}

textarea {
	display: block;
	width: 100%;
	height: 125px;
	border-radius: 4px;
	width: 100%;
	padding: 5px 10px;
	border: 1px solid rgba(0, 0, 0, 0.25);
	margin-bottom: 25px;
}

[type="submit"]{
	margin-bottom: 0;
	display: block;
	width: 100%;
	height: 40px;
	line-height: 40px;
	border-radius: 4px;
	border: 1px solid rgba(0, 0, 0, 0.25);
	padding: 0;
}

.message {
	text-align: center;
	height: 420px;
	max-width: 100%;
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}
